body, .pfe-c-content p {
  font-family: var(--pfe-theme--font-family, "Red Hat Text", "RedHatText", "Overpass", Overpass, Arial, sans-serif);
  line-height: var(--pfe-theme--line-height, 1.5);
  font-weight: var(--pfe-theme--font-weight--light, 300);
}

.pfe-c-content dl,
.pfe-c-content ol,
.pfe-c-content ul {
  padding-left: calc(var(--pfe-theme--ui--indent, 20px) * 2);
}

.pfe-c-content dl:not(:last-child):not(:empty),
.pfe-c-content ol:not(:last-child):not(:empty),
.pfe-c-content ul:not(:last-child):not(:empty) {
  margin-bottom: var(--pfe-theme--content-spacer--body--md, 1rem);
}

.pfe-c-content dd:not(:last-child):not(:empty),
.pfe-c-content dt:not(:last-child):not(:empty),
.pfe-c-content li:not(:last-child):not(:empty) {
  margin-bottom: var(--pfe-theme--content-spacer--body--sm, 0.5rem);
}

.pfe-c-content li > ol,
.pfe-c-content li > ul {
  margin-top: var(--pfe-theme--content-spacer--body--sm, 0.5rem);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

:root,
body,
p,
blockquote,
figure,
fieldset,
legend,
textarea,
pre,
iframe,
hr,
h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin-top: 0;
}

:root:last-child,
body:last-child,
p:last-child,
blockquote:last-child,
figure:last-child,
fieldset:last-child,
legend:last-child,
textarea:last-child,
pre:last-child,
iframe:last-child,
hr:last-child,
h1:last-child,
h2:last-child,
h3:last-child,
h4:last-child,
h5:last-child,
h6:last-child {
  margin-bottom: 0;
}

button,
input,
select,
textarea {
  margin: 0;
  font-family: inherit;
}

img,
embed,
iframe,
object,
audio,
video {
  max-width: 100%;
  height: auto;
}

iframe {
  border: 0;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
}

td,
th {
  padding: 0;
  text-align: left;
}

:root {
  --pfe-broadcasted--text: var(--pfe-theme--color--text, #151515);
  --pfe-broadcasted--link: var(--pfe-theme--color--link, #06c);
  --pfe-broadcasted--link--hover: var(--pfe-theme--color--link--hover, #004080);
  --pfe-broadcasted--link--focus: var(--pfe-theme--color--link--focus, #004080);
  --pfe-broadcasted--link--visited: var(--pfe-theme--color--link--visited, #6753ac);
  --pfe-broadcasted--link-decoration: var(--pfe-theme--link-decoration, none);
  --pfe-broadcasted--link-decoration--hover: var(--pfe-theme--link-decoration--hover, underline);
  --pfe-broadcasted--link-decoration--focus: var(--pfe-theme--link-decoration--focus, underline);
  --pfe-broadcasted--link-decoration--visited: var(--pfe-theme--link-decoration--visited, none);
  font-size: var(--pf-global--FontSize--root, 1em);
}

body {
  /* This variable is intentionally left empty for light DOM within components */
  font-size: var(--pf-c--FontSize, var(--pf-c-content--FontSize, var(--pf-global--FontSize--md, 1rem)));
  color: var(--pfe-theme--color--text, #151515);
  text-align: left;
  margin: 0;
}

.pfe-c-content h1 {
  font-family: var(--pfe-theme--font-family--heading, "Red Hat Display", "RedHatDisplay", "Overpass", Overpass, Arial, sans-serif);
  line-height: var(--pfe-theme--line-height--heading, 1.3);
  font-weight: var(--pfe-theme--font-weight--normal, 400);
  font-size: var(--pf-c--FontSize, var(--pf-c-content--h1--FontSize, var(--pf-global--FontSize--3xl, 1.75rem)));
}

.pfe-c-content h1:not(:last-child):not(:empty) {
  margin-bottom: var(--pfe-theme--content-spacer--heading--sm, 1rem);
}

.pfe-c-content h2 {
  font-family: var(--pfe-theme--font-family--heading, "Red Hat Display", "RedHatDisplay", "Overpass", Overpass, Arial, sans-serif);
  line-height: var(--pfe-theme--line-height--heading, 1.3);
  font-weight: var(--pfe-theme--font-weight--normal, 400);
  font-size: var(--pf-c--FontSize, var(--pf-c-content--h2--FontSize, var(--pf-global--FontSize--2xl, 1.5rem)));
}

.pfe-c-content h2:not(:last-child):not(:empty) {
  margin-bottom: var(--pfe-theme--content-spacer--heading--sm, 1rem);
}

.pfe-c-content h3 {
  font-family: var(--pfe-theme--font-family--heading, "Red Hat Display", "RedHatDisplay", "Overpass", Overpass, Arial, sans-serif);
  line-height: var(--pfe-theme--line-height--heading, 1.3);
  font-weight: var(--pfe-theme--font-weight--normal, 400);
  font-size: var(--pf-c--FontSize, var(--pf-c-content--h3--FontSize, var(--pf-global--FontSize--xl, 1.25rem)));
}

.pfe-c-content h3:not(:last-child):not(:empty) {
  margin-bottom: var(--pfe-theme--content-spacer--heading--sm, 1rem);
}

.pfe-c-content h4 {
  font-family: var(--pfe-theme--font-family--heading, "Red Hat Display", "RedHatDisplay", "Overpass", Overpass, Arial, sans-serif);
  line-height: var(--pfe-theme--line-height--heading, 1.3);
  font-weight: var(--pfe-theme--font-weight--normal, 400);
  font-size: var(--pf-c--FontSize, var(--pf-c-content--h4--FontSize, var(--pf-global--FontSize--lg, 1.125rem)));
}

.pfe-c-content h4:not(:last-child):not(:empty) {
  margin-bottom: var(--pfe-theme--content-spacer--heading--sm, 1rem);
}

.pfe-c-content h5 {
  font-family: var(--pfe-theme--font-family--heading, "Red Hat Display", "RedHatDisplay", "Overpass", Overpass, Arial, sans-serif);
  line-height: var(--pfe-theme--line-height--heading, 1.3);
  font-weight: var(--pfe-theme--font-weight--normal, 400);
  font-size: var(--pf-c--FontSize, var(--pf-c-content--h5--FontSize, var(--pf-global--FontSize--md, 1rem)));
}

.pfe-c-content h5:not(:last-child):not(:empty) {
  margin-bottom: var(--pfe-theme--content-spacer--heading--sm, 1rem);
}

.pfe-c-content h6 {
  font-family: var(--pfe-theme--font-family--heading, "Red Hat Display", "RedHatDisplay", "Overpass", Overpass, Arial, sans-serif);
  line-height: var(--pfe-theme--line-height--heading, 1.3);
  font-weight: var(--pfe-theme--font-weight--normal, 400);
  font-size: var(--pf-c--FontSize, var(--pf-c-content--h6--FontSize, var(--pf-global--FontSize--sm, 0.875rem)));
}

.pfe-c-content h6:not(:last-child):not(:empty) {
  margin-bottom: var(--pfe-theme--content-spacer--heading--sm, 1rem);
}

.pfe-c-content p {
  font-size: var(--pf-c--FontSize, var(--pf-c-content--FontSize, var(--pf-global--FontSize--md, 1rem)));
}

.pfe-c-content p:not(:last-child):not(:empty) {
  margin-bottom: var(--pfe-theme--content-spacer--body--sm, 0.5rem);
}

a {
  color: var(--pfe-broadcasted--link, #06c);
  text-decoration: var(--pfe-broadcasted--link-decoration, none);
}

a:hover {
  color: var(--pfe-broadcasted--link--hover, #004080);
  text-decoration: var(--pfe-broadcasted--link-decoration--hover, underline);
}

a:focus {
  color: var(--pfe-broadcasted--link--focus, #004080);
  text-decoration: var(--pfe-broadcasted--link-decoration--focus, underline);
}

a:visited {
  color: var(--pfe-broadcasted--link--visited, #6753ac);
  text-decoration: var(--pfe-broadcasted--link-decoration--visited, none);
}

button,
a {
  cursor: pointer;
}
